
@import "default";


    .round-border-container {
        border: 2px solid $BorderGreyColor;
        border-radius: 10px;
    }
    
    .image-section {
        width: 700px;
        position: relative;

        img {
            @include PhotoSliderContainer (0px);

        }
    }

    .slider-text-content {
        display: flex;
        flex-direction: column;
        row-gap: 11px;


        .title{
            margin-bottom: 0;
        }

        .accordion {
            display: flex;
            gap: 40px;

            .accordion-icon {
                position: relative;
                display: flex;
                flex-direction: column;
                align-items: center;
                gap: 0px;
                width: 20px;
                overflow: hidden;

                .ellipse {
                    width: 75%;
                    height: 15px;
                    border-radius: 50%;
                    border: 2px solid $AccordeonColor;
                    transition: background-color 0.5s ease-in-out; 
                }

                .line{
                    position: absolute;
                    margin-top: 17px;
                    width: 2px;
                    height: 100%;
                    background-color: $AccordeonColor;
                    overflow: hidden;
                    max-height: 0;
                    opacity: 0;
                    transition: max-height 0.5s ease-out, opacity 0.5s ease-out;
                }   
            } 

            .accordion-text {
                display: flex;
                flex-direction: column;
                gap: 11px;

                .title-text {
                    font-size: 28px;
                    margin: 0;
                    cursor: pointer;
                }
    
                .accordion-content {
                    overflow: hidden;
                    max-height: 0;
                    opacity: 0;
                    transition: max-height 0.5s ease-out, opacity 0.5s ease-out;

                    p{
                        margin: 0;
                        font-size: 16px;
                    }


                }
            }
            
            &.active .ellipse .accordion-content{
                .ellipse {
                    background-color: $AccordeonColor;
                }

                .line {
                    max-height: 200px;
                    opacity: 1;
                }

                .accordion-content {
                    opacity: 1;
                }
            }

        }
        

        .title {
            @include Title(Roboro-Regular, 46px)
        }

    }


header {
    background-image: url(../img/main-green-background.png);
