            width: 100%;
        }
    }
}

body .fresh-production-section {
    @include MainPageSliderContainer(89px, 820px);

    .image-section {
        width: 48.9%;
        height: 100%;

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    .production-section {
        margin-top: 61px;
        padding-right: 20px;

        .accordion-text {
            width: 418px;
        }
    }
}

body .frozen {
    @include MainPageSliderContainer(99px, 619px); 

    .slider-img-container{
        width: 48%;
        display: flex;
        align-items: center;
        position: relative;

        .slider-img-item {
            width: 82%;
            position: absolute;
            transition: z-index 0.3s ease, transform 0.4s ease, scale 0.4s ease;
            will-change: transform, scale,  z-index;
            
            img {
                height: 100%;
                width: 100%; 
            }

            &.selected {
                height: 100%;
                z-index: 5;
                transform: translateX(0) scale(1);
            }
            &.second {
                z-index: 4;
                height: 93.3%;
                transform: translateX(10%) scale(0.95);
            }
            &.third {
                z-index: 3;
                height: 86.6%;
                transform: translateX(20%) scale(0.9);
            }
            &.fourth {
                z-index: 2;
                height: 79.9%;
                transform: translateX(30%) scale(0.85);
            }
            &.fifth {
                z-index: 1;
                height: 73.2%;
                transform: translateX(40%) scale(0.8);
            }   

            
        }
    }

    .slider-text-content .title{
        margin-top: 30%;
        margin-bottom: 15px;
    }
}

body .production {
    display: flex;
    align-items: center;
    flex-direction: column;
